@import url(https://fonts.googleapis.com/css2?family=Quicksand&display=swap);
@import url(https://fonts.googleapis.com/css?family=Raleway:300);
:root {
  --primary: #ebbb00;
  --secondary: #808080;
  --third: red;
  font-family: "Montserrat", sans-serif;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  overflow-x: hidden;
}

.loading {
  text-transform: capitalize;
  text-align: center;
  margin-top: 3rem;
}

/* LINKS */

.btn-hover {
  width: 200px;
  font-size: 16px;
  font-weight: 600;
  color: #fff;
  cursor: pointer;
  margin: 20px;
  height: 55px;
  text-align: center;
  border: none;
  background-size: 300% 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  border-radius: 50px;
  moz-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}

.btn-hover:hover {
  background-position: 100% 0;
  moz-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}

.btn-hover:focus {
  outline: none;
}

.btn-hover.color-8 {
  background-image: linear-gradient(
    to right,
    #29323c,
    #485563,
    #2b5876,
    #4e4376
  );
  box-shadow: 0 4px 15px 0 rgba(45, 54, 65, 0.75);
}

/* BUTTON NAVBAR */
.btn-nav {
  width: 150px;
  font-size: 16px;
  font-weight: 600;
  color: #fff;
  cursor: pointer;
  height: 45px;
  text-align: center;
  border: none;
  background-size: 300% 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 15px;

  border-radius: 50px;
  moz-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}

.btn-nav:hover {
  background-position: 100% 0;
  moz-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}

.btn-nav:focus {
  outline: none;
}

.btn-nav.color-8 {
  background-image: linear-gradient(
    to right,
    #29323c,
    #485563,
    #2b5876,
    #4e4376
  );
  box-shadow: 0 4px 15px 0 rgba(45, 54, 65, 0.75);
}

.navbar {
  height: 60px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 1rem 2rem;
  z-index: 100;
  position: fixed;
  width: 100%;
  background: transparent;
  font-weight: lighter;

  /* TRANSITION */
  /* WebKit */
  /* Opera */
  /* Standard */
  transition: all 1s ease-in;
}

.navbar.active {
  background: rgba(0, 0, 0, 5);
}

.navbar-logo {
  height: 20px;
}

a {
  text-decoration: none;
}

.navbar-link {
  color: white;
  cursor: pointer;
  height: 100%;
  padding: 1rem;
  text-transform: uppercase;
  font-weight: normal;
  letter-spacing: 4px;
}

.navbar-link:hover {
  -webkit-transform: translateY(1px);
          transform: translateY(1px);
  color: var(--primary);
  /* border-bottom: 4px solid var(--primary); */
  transition: all 0.3s ease-out;
}

.burger {
  display: none;
}
.burger > i {
  cursor: pointer;
  position: absolute;
  top: 0;
  right: 0;
  -webkit-transform: translate(-70%, 50%);
          transform: translate(-70%, 50%);
  color: white;
  font-size: 30px;
}

.nav-menu {
  display: flex;
  align-items: center;
}

.navbar-button {
  background-color: var(--primary);
  color: black;
  border: none;
  outline: none;
  white-space: nowrap;
  cursor: pointer;
  text-decoration: none;
  transition: 0.3s;
  padding: 8px 10px;
  font-size: 20px;
  border-radius: 15px;
}
.navbar-button:hover {
  background-color: black;
  border: 2px solid var(--primary);
  color: white;
  box-shadow: inset 0px 0px 10px var(--primary);
}

@media screen and (max-width: 1024px) {
}
@media screen and (max-width: 960px) {
  .nav-menu {
    display: none;
  }
  .burger {
    display: block;
  }
}

.dropdown {
  position: fixed;
  z-index: 999;
  width: 100%;
  height: 100%;
  background: black;
  display: grid;
  align-items: center;
  top: 0;
  left: 0;
  transition: 0.3s ease-in-out;
  opacity: 1;
  top: 0;
}
.dropdown-closed {
  position: fixed;
  z-index: 999;
  width: 100%;
  height: 100%;
  background: black;
  display: grid;
  align-items: center;
  top: 0;
  left: 0;
  transition: 0.3s ease-in-out;
  opacity: 1;
  top: -100%;
}

.dropdown-icon {
  position: absolute;
  top: 1.2rem;
  right: 1.5rem;
  background: transparent;
  font-size: 2rem;
  cursor: pointer;
  outline: none;
}

.close-icon,
.burguer {
  color: white;
  position: fixed;
  z-index: 1000;
  right: 1rem;
  top: 1rem;
  font-size: 1.5rem;
  opacity: 0;
  cursor: pointer;
}

.close-icon {
  color: red;
}

.dropdown-menu {
  margin-bottom: 3rem;
  /* display: grid; */
  /* grid-template-columns: 1fr; */
  /* grid-template-rows: repeat(4, 80px); */
  /* text-align: center; */
  /* margin-bottom: 4rem; */
}

.dropdown-link {
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 1.5rem;
  list-style: none;
  cursor: pointer;
  transition: 0.2s ease-in-out;
}

.dropdown-link:hover {
  color: var(--primary);
}
.dropdown-btn-wrap {
  display: flex;
  justify-content: center;
}

/* BUTTON */
.dropdown-button {
  /* background-color: var(--secondary); */
  background-color: var(--primary);
  /* color: white; */
  color: black;
  border: none;
  outline: none;
  /* min-width: 100px;
    max-width: 200px; */
  white-space: nowrap;
  cursor: pointer;
  text-decoration: none;
  transition: 0.3s;
  padding: 8px 10px;
