.hero {
  height: 90vh;
  position: relative;
  overflow: hidden;
}

.hero-wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  position: relative;
}

