.dropdown {
  position: fixed;
  z-index: 999;
  width: 100%;
  height: 100%;
  background: black;
  display: grid;
  align-items: center;
  top: 0;
  left: 0;
  transition: 0.3s ease-in-out;
  opacity: 1;
  top: 0;
}
.dropdown-closed {
  position: fixed;
  z-index: 999;
  width: 100%;
  height: 100%;
  background: black;
  display: grid;
  align-items: center;
  top: 0;
  left: 0;
  transition: 0.3s ease-in-out;
  opacity: 1;
  top: -100%;
}

.dropdown-icon {
  position: absolute;
  top: 1.2rem;
  right: 1.5rem;
  background: transparent;
  font-size: 2rem;
  cursor: pointer;
  outline: none;
}

.close-icon,
.burguer {
  color: white;
  position: fixed;
  z-index: 1000;
  right: 1rem;
  top: 1rem;
  font-size: 1.5rem;
  opacity: 0;
  cursor: pointer;
}

.close-icon {
  color: red;
}

.dropdown-menu {
  margin-bottom: 3rem;
  /* display: grid; */
  /* grid-template-columns: 1fr; */
  /* grid-template-rows: repeat(4, 80px); */
  /* text-align: center; */
  /* margin-bottom: 4rem; */
}

.dropdown-link {
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 1.5rem;
  list-style: none;
  cursor: pointer;
  transition: 0.2s ease-in-out;
}

.dropdown-link:hover {
  color: var(--primary);
}
.dropdown-btn-wrap {
  display: flex;
  justify-content: center;
}

/* BUTTON */
.dropdown-button {
  /* background-color: var(--secondary); */
  background-color: var(--primary);
  /* color: white; */
  color: black;
  border: none;
  outline: none;
  /* min-width: 100px;
    max-width: 200px; */
  white-space: nowrap;
  cursor: pointer;
  text-decoration: none;
  transition: 0.3s;
  padding: 8px 10px;
  font-size: 20px;
  /* text-shadow:0px 1px 0px var(--primary);  */
  /* box-shadow: 0px 10px 14px -7px var(--primary); */
  border-radius: 8px;
}
.dropdown-button:hover {
  background-color: black;
  border: 2px solid var(--primary);
  color: white;
  box-shadow: inset 0px 0px 10px var(--primary);
}

@media screen and (max-width: 960px) {
  .burguer,
  .close-icon {
    opacity: 1;
  }
  .dropdown-menu {
    grid-template-rows: repeat(4, 60px);
  }
}
